import React from "react";
import { Form } from "react-bootstrap";

interface Props {
  value?: string;
  onChange?: (event: React.ChangeEvent<HTMLTextAreaElement>) => void;
  placeholder?: string;
  [key: string]: any; // Принимаем остальные пропсы
}

const DescriptionField: React.FC<Props> = ({
  value,
  onChange,
  placeholder = "",
  ...rest
}) => (
  <Form.Control
    as="textarea"
    rows={2}
    value={value || ""}
    onChange={onChange}
    placeholder={placeholder}
    {...rest} // Передаем все остальные пропсы (включая name из react-hook-form)
  />
);

export default DescriptionField;
