import React from "react";
import { useForm } from "react-hook-form";
import { Button, Form } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { addContact } from "../store/reducer";
import ContactTypeSelect from "../atoms/ContactTypeSelect";
import TextInput from "../atoms/TextInput";
import DescriptionField from "../atoms/DescriptionField";
import { ContactType } from "../store/types";

interface Props {
  handleClose: () => void;
  handleAlert: (msg: string) => void;
}

interface FormValues {
  type: ContactType;
  value: string;
  description: string;
}

const ContactForm: React.FC<Props> = ({ handleClose, handleAlert }) => {
  const dispatch = useDispatch();
  const { register, handleSubmit, reset } = useForm<FormValues>({
    defaultValues: { type: "phone", value: "", description: "" },
  });

  const onSubmit = (data: FormValues) => {
    dispatch(addContact(data));
    handleAlert("Contact added!");
    reset();
    setTimeout(handleClose, 1500);
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <Form.Group className="mb-2">
        <Form.Label>Тип</Form.Label>
        <ContactTypeSelect {...register("type")} />
      </Form.Group>
      <Form.Group className="mb-2">
        <Form.Label>Контакт</Form.Label>
        <TextInput {...register("value")} />
      </Form.Group>
      <Form.Group className="mb-2">
        <Form.Label>Описание</Form.Label>
        <DescriptionField {...register("description")} />
      </Form.Group>
      <Button type="submit">Submit</Button>
    </Form>
  );
};

export default ContactForm;
